.about{
    display: grid;
    grid-template-columns: 30% 50%;
    gap: 15%;
}
.mine_img{
    border-radius: 2rem;
    overflow: hidden;
    transition: var(--transition);
}

.about_me{
    width: 100%;
    border-radius: 2rem;
    display: grid;
    place-items: center;    
}

.card_container{
    display: grid;
    grid-template-columns:repeat(3, 1fr);
    gap: 2rem;
}

.card{
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.card:hover{
    background: transparent;
    background-color: var(--color-primary-variant);
}

.card h5{
    font-size: 1rem;
}

.card small{
    font-size: 0.7rem;
    color: beige;
}

.about_container p{
    margin: 3rem 0 2.6rem;;
}

@media screen and (max-width: 1024px) {
     .about{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_container p{
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .card_container{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about_container{
        text-align: center;
    }

    .about_container{
        margin: 1.5rem 0;
    }
}