header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden ;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
/*Button class*/
.buttoncls{
    margin-top: 1.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*Icons class*/
.header_essential{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0%;
    bottom: 3rem;
}

.header_essential::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*image*/

.mefull{
    background:linear-gradient(var(--color-primary),transparent);
    width: 20rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
}

.scroll_down{
    position: absolute;
    right: -2.5rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 200;

}

@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }

    .header_essential, .scroll_down{
        display: none;
    }
}